import React from 'react'
import { connect } from 'react-redux'
import { startClock, serverRenderClock } from '../store'
import Examples from '../components/examples'

class Index extends React.Component {
  static getInitialProps ({ reduxStore, req }) {
    const isServer = !!req
    reduxStore.dispatch(serverRenderClock(isServer))

    return {}
  }

  componentDidMount () {
    const { dispatch } = this.props
    this.timer = startClock(dispatch)
  }

  componentWillUnmount () {
    clearInterval(this.timer)
  }

  render () {
    return (
      <div className="container">
        <Examples />
        <div>We'll put all the little clouds in and let them dance around and have fun. I was blessed with a very steady hand; and it comes in very handy when you're doing these little delicate things. Don't kill all your dark areas - you need them to show the light. As trees get older they lose their chlorophyll. It's a super day, so why not make a beautiful sky? We have a fantastic little sky!</div>
        <button className="btn btn-danger">Hey</button>
      </div>
    )
  }
}

export default connect()(Index)
